import React from 'react';
import {Grid, Dialog, DialogTitle,DialogContent, CircularProgress} from '@material-ui/core';
import { useState, useEffect} from 'react';
import axios from 'axios';
import './Calendar.css';

import './referral.css';
import '../../bootstrap.min.css';
import referIcon from '../../assets/refer.svg';
import clipFunnel from '../../assets/clip-funnel.svg';
import copyClip from '../../assets/copy-clip.svg';
import { CopyToClipboard } from "react-copy-to-clipboard";
/* import TotalReward from "./totalReward";
// import Withdraw from "./withdraw";
import TotalRefer from "./totalReferral";
 */
import "./referral.css";
import Login from '../Header/Login'
/* import referIcon from '../../assets/refer.svg'; */

export default function Referral() {
	const [status, setStatus] = useState({
		logged:false,
		isCopied:false,
		address:'',
		referalCount:0,
		rewards:0
	})
	
	const onLogin = (address, referalCount, rewards) => {
		setStatus({...status, logged:true, address, referalCount, rewards})
	}
	return (
		<div style = {{marginTop:'5%'}}>
			<Grid container style={{ gap: 35 }}>
				<Grid item xs={12} sm = {12} md = {1} lg={1}></Grid>
				<Grid item xs = {12} sm = {12} md = {4} lg={4}>
					<div className = "referral-left text-center">
						<div className = "refer-title">REFER friends and get REWARDs
							<span style = {{marginLeft:'16px'}}><img src = {referIcon}></img></span>
						</div>
						<div className = "refer-statement">Share the referral link below to invite your friends and 💰 EARN 10% of your friends' purchasing amount.</div>
					</div>
					{/* <div className = "calendar-style"><Calendar /></div> */}
					
				</Grid>
				<Grid item xs = {12} sm = {12} md = {6} lg={6}>
				<div className = "get-link">
					<div className = "get-link-title">Get your referral link</div>
					{status.logged?(
						<a href = {`${window.location.origin}/?referrer=${status.address}`} target = "_blank">
							<div className = "git-link-url">
								{`${window.location.origin}/?referrer=${status.address}`}
							<span style = {{marginLeft:'10px'}}><img src = {clipFunnel}></img></span>
							</div>
						</a>
					) : null}
					<CopyToClipboard text = {`${window.location.origin}/?referrer=${status.address}`}>
						{status.logged?(
							<button className = "get-link-button" onClick={() => {
								setStatus({...status, isCopied:true})
								setTimeout(() => setStatus({...status, isCopied:false}), 1000);
							}}>
								<span><img src = {copyClip} style = {{marginRight:'10px'}}></img></span>
								<span>{status.isCopied ? "COPIED TO CLIPBOARD" : "COPY REFERRAL LINK"}</span>
							</button>
						):(
							<button className = "get-link-button1" disabled = {true} style = {{opacity:'0.1'}}>
								<span><img src = {copyClip} style = {{marginRight:'10px'}}></img></span>
								<span>{status.isCopied ? "COPIED TO CLIPBOARD" : "COPY REFERRAL URL"}</span>
							</button>
						)}
					</CopyToClipboard>
				</div>
					<Grid container style = {{marginTop:'10%'}}>
						<Grid item xs={12} sm = {12} md = {6} lg={6}>
							<div className = "total-referral">
								<div className = "total-referral-title">Total Referrals</div>
								<div className = "total-referral-count">
									{status.referalCount}
									<span style = {{marginLeft:'20px'}}><img src = {referIcon} width = "6%"></img></span>
								</div>
							</div>
						</Grid>
						<Grid item xs={12} sm = {12} md = {6} lg={6}>
							<div className = "total-reward">
								<div className = "total-reward-title">
									<span>Total Rewards 💰</span>
								</div>
								<div className = "total-reward-count text-right">
									{status.rewards}
									<span style = {{marginLeft:'20px', color:'#26e3ff'}}>USDT</span>
								</div>
							</div>
						</Grid>
					</Grid>
					{/* <Withdraw /> */}
				</Grid>
				<Grid item xs={12} sm = {12} md = {1} lg={1}></Grid>
			</Grid>
			<Login onAction={onLogin} />
		</div>
	);
}