import React from 'react';
import {Dialog, DialogTitle,DialogContent, CircularProgress} from '@material-ui/core';
import { useState, useEffect} from 'react';
import axios from 'axios';

import '../referral/referral.css';
import '../../bootstrap.min.css';

export const getAddress = () => {
    return window.sessionStorage.getItem("publickey")
}
const setAddress = (publicKey) => {
    window.sessionStorage.setItem("publickey", publicKey)
}

export default (props) => {
	const refKey = React.useRef(null)
	const [status, setStatus] = useState({
        logged: false,
		loading:false,
		errMsg:''
	})
	useEffect(() => {
		const address =  getAddress()
		if(address){
			getReferInfo(address)
		} else {
			setStatus({...status, logged:false})
		}
	}, []);
    const getReferInfo = async (address) => {
		try {
			const url = process.env.REACT_APP_PROXY + `/api/get/user`
			const res = await axios.post(url, {address})
			const data = res && res.data;
			if (data && data.status==='ok') {
				setStatus({...status, logged:true, loading:false})	
                if (props.onAction) {
                    props.onAction(address, data.count || 0, data.rewards || 0)
                }
			} else {
				setStatus({...status,logged:false,  errMsg:'', loading:false})	
			}
		} catch (err) {
			
		}
	}
	const onLogin = async () => {
		if (!status.address) {
			return refKey.current.focus()
		}
		if (!/^0x[a-fA-F0-9]{40}$/.test(status.address)) {
			refKey.current.select();
			return refKey.current.focus()
		}
		setStatus({...status, loading:true})

		const url = process.env.REACT_APP_PROXY + `/api/add/user`
		const res = await axios.post(url, {address: status.address})
		const data = res && res.data;
		if (data && data.status==='ok') {
			setAddress(status.address)
			setStatus({...status, logged:true, loading:false})
            if (props.onAction) {
                props.onAction(status.address, data.count || 0, data.rewards || 0)
            }
		} else {
			setStatus({...status, errMsg:'', logged:false, loading:false})	
		}
		
	}
	return (
        <Dialog open={!status.logged}>
            <DialogTitle>Login</DialogTitle>
            <DialogContent className="login-dialog" >
                <div style={{marginBottom:20}}>
                    <input ref={refKey} className = "address" onChange = {(e)=>setStatus({...status, address:e.target.value})} value = {status.address} type = "text" name = "address" placeholder = "Enter Your Public Key" required></input>
                </div>
                <div style={{color:'red', textAlign:'center'}}>
                    {status.errMsg}
                </div>
                <div style={{marginBottom:20}}>
                    <button onClick={onLogin} className = "button-1" type="submit" style={{display:'flex', justifyContent:'center', alignItems:'center'}}>
                        {status.loading && <CircularProgress size={14} style={{color:'white', marginRight:10}} />}
                        LogIn
                    </button>
                </div>
            </DialogContent>
        </Dialog>
	);
}
